<template>
  <div class="c-validSlide" :style="{width:slideWidthStyle}">
    <div class="c-validSlide--btn" @mousedown="run&&slideInit($event)" :style="{left:btnLeft}" :class="{success:isSuccess,error:isError,active:isActive}">{{btnText}}</div>
    <div class="c-validSlide--body">{{bodyText}}</div>
  </div>
</template>

<script>
const TEXT = '向右滑动完成验证'
export default {
  name: 'ValidSlide',
  data () {
    return {
      isMove: false,
      startX: 0,
      moveX: 0,
      btnLeft: 0,
      btnWidth: 40,
      slideWidthStyle: null,
      slideWidth: '',
      slideDistance: '',
      run: true,
      isSuccess: false,
      isError: false,
      isActive: false,
      btnText: '>',
      bodyText: TEXT
    }
  },
  props: {
    width: {
      type: null,
      default: '100%'
    }
  },
  created () {
    this.slideWidthStyle = this.width
  },
  mounted () {
    // let $btn = this.$el.querySelector('.c-validSlide--btn')
    let $slide = this.$el
    this.slideWidth = $slide.offsetWidth
    this.slideWidthStyle = this.slideWidth + 'px'
    this.slideDistance = this.slideWidth - this.btnWidth - 2
    console.info(this.slideDistance)
    document.addEventListener('mousemove', this.slideMove)
    document.addEventListener('mouseup', this.slideUp)
  },
  methods: {
    slideInit (e) {
      this.isMove = true
      this.startX = this.getTouches(e).x
      this.bodyText = ''
      this.isActive = true
    },
    slideMove (e) {
      if (!this.isMove) {
        return
      }
      this.moveX = this.getTouches(e).x
      let btnLeft = this.moveX - this.startX
      this.btnLeft = btnLeft > this.slideDistance ? this.slideDistance + 'px' : btnLeft + 'px'
    },
    slideUp (e) {
      this.isMove = false
      this.isActive = false
      if (this.btnLeft === this.slideDistance + 'px') {
        console.info('成功')
        this.bodyText = '验证成功'
        this.btnText = ''
        this.isSuccess = true
        this.run = false
        document.removeEventListener('mousemove', this.slideMove)
        document.removeEventListener('mouseup', this.slideUp)
        this.$emit('valid-success')
      } else {
        this.isError = true
        this.btnText = ''
        setTimeout(() => {
          this.btnLeft = 0
          this.startX = 0
          this.moveX = 0
          this.isError = false
          this.btnText = '>'
          this.bodyText = TEXT
          this.$emit('valid-error')
        }, 300)
      }
    },
    getTouches (e) {
      if (e.touches !== undefined) {
        return {
          x: e.touches[0].pageX,
          y: e.touches[0].pageY
        }
      }

      if (e.touches === undefined) {
        if (e.pageX !== undefined) {
          return {
            x: e.pageX,
            y: e.pageY
          }
        }
        if (e.pageX === undefined) {
          return {
            x: e.clientX,
            y: e.clientY
          }
        }
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.c-validSlide{
  width:100%;
  height:40px;
  line-height: 40px;
  border:1px solid #ccc;
  position: relative;
  user-select: none;
}
.c-validSlide--body{
  text-align: center;
}
.c-validSlide--btn{
  width:40px;
  height: 40px;
  line-height: 40px;
  border-left:1px solid #ccc;
  border-right:1px solid #ccc;
  position: absolute;
  background: #fff;
  user-select: none;
  text-align: center;

}
.c-validSlide--btn:hover,
.c-validSlide--btn.active{
  background: #409EFF;
  color:#fff;
  border-color:#409EFF;
}
.c-validSlide--btn.success{
  border-color:#13CE66;
  background:#13CE66 url(data:image/svg+xml;base64,) no-repeat 0 50%;
}
.c-validSlide--btn.error{
  border-color:#FF4949;
  background:#FF4949 url(data:image/svg+xml;base64,) no-repeat 0 50%;
}
</style>



