
<template>
<div style="padding:30px;">
  <div style="margin-bottom:10px;"><input type="file" name="file" @change="openCrop1($event)" @dragover.stop.prevent @drag.stop.prevent="dragFn"></div>
  <vue-crop
    :crop-url="cropUrl1"
    :ratio="ratio"
    :height="460"
    :width="460"
    :previewJson="previewJson1"
    class="c-crop--preview_right"
    @afterCrop="afterCrop"
  >
  </vue-crop>
  <div style="margin-bottom:10px;margin-top:60px;"><input type="file" name="file" @change="openCrop2($event)" @dragover.stop.prevent @drag.stop.prevent="dragFn"></div>
  <vue-crop
    :crop-url="cropUrl2"
    :height="460"
    :width="460"
    class="c-crop--preview_right"
    @afterCrop="afterCrop"
  >
  </vue-crop>
</div>
</template>

<script>
// 可以全局调用，也可以单页面调用
// import {VueCrop} from 'src/components/index.js'
// import Vue from 'vue'
// Vue.use(VueCrop)
export default {
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      cropUrl2: 'data:image/svg+xml;base64,',
      cropUrl1: '',
      previewJson1: [{
        width: 50,
        height: 50
      }, {
        width: 100,
        height: 100
      }]
    }
  },
  props: {
    ratio: {
      type: Number,
      default: 1
    }
  },
  methods: {
    // 裁切完图片选择回调
    afterCrop (coord, url) {
      console.info('afterCrop', coord)
    },
    // 显示裁切
    openCrop1 (event) {
      let _this = this
      this.fileLoader(event, src => {
        _this.cropUrl1 = src
      })
      // this.cropUrl = src
    },
    // 显示裁切
    openCrop2 (event) {
      let _this = this
      this.fileLoader(event, src => {
        _this.cropUrl2 = src
      })
      // this.cropUrl = src
    },
    dragFn (event) {
      const imgFile = event.dataTransfer.files.item(0)
      if (!(/^image.*$/.test(imgFile.type))) {
        alert('您还未拖拽任何图片过来,或者您拖拽的不是图片文件')
        return false
      }

      var fReader = new FileReader()
      fReader.readAsDataURL(imgFile)
      fReader.onload = function (event) {
        this.cropUrl = event.target.result
      }
    },
    fileLoader (event, cb) {
      // 判断浏览器是否有FileReader接口
      if (typeof FileReader === 'undefined') {
        // 如果浏览器是ie
        if (window.ActiveXObject) {
          if (event.target.files) {
            cb(event.target.files.item(0).getAsDataURL())
          } else {
            cb(event.target.value)
          }
        }
      } else {
        var file = event.target.files[0]
        var reader = new FileReader()
        reader.onload = function (e) {
          cb(e.target.result)
        }
        reader.readAsDataURL(file)
      }
    }
  }
}
</script>

<style scoped>

</style>



